.App{
  min-height: 100vh;
  background-image: linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%);
}

.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  width: 50%;
  border: none;
  padding: 10px;
  outline: none;
}


.searchButton {
  background: rgb(47, 233, 47);
  border: none;
  padding: 10px 20px; 
  color: white;
  transition: 0.2s;
  outline: none;
}

.searchButton:hover {
  transform: scale(1.03);
  cursor: pointer;

}

.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  
}