.recipe {
    border-radius: 10%;
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40%;
    padding: 5px;
    transition: 0.1s;
}

.recipe:hover {
    transform: scale(1.03);
    box-shadow: 0px 5px 15px rgb(71, 71, 71);
}

.image {
    border-radius: 10%;
    width: 10em;
    height: auto;
}